import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Vendor } from '../vendor';
import { ListVendorsService } from './list-vendors.service';
import { LogoutService } from '../logout.service';

@Component({
  selector: 'app-list-vendors',
  templateUrl: './list-vendors.component.html',
  styleUrls: ['./list-vendors.component.scss']
})
export class ListVendorsComponent implements OnInit {
  vendors:Vendor[];
  result :string;
  constructor(private router:Router,private logout: LogoutService,private listVendorService:ListVendorsService) { }

  ngOnInit() {
    this.getVendors();
  }

  getVendors():void{
    this.listVendorService.getAllVendors()
    .subscribe((vendorData)=>{
      this.vendors=vendorData
    },(error)=>{
      console.log(error);
    });
  }

  disable(vendor:Vendor){
    this.listVendorService.disableVendor(vendor)
    .subscribe((response)=>{
      this.getVendors();
    }, (error) =>{
      console.log(error);
    });
  }

  searchVendor(search:string): void{
    this.result="";
    if (search!=""){
      
    this.listVendorService.searchVendorByString(search)
    .subscribe((response)=>{
      this.vendors=response
      if(this.vendors.length==0)
      {
        this.result="No Result";
      }
    }, (error) =>{
      console.log(error);
    });
  }else{
    this.getVendors();
  }
  }

  edit(vendorId:number){
    this.router.navigate(['editVendor/'+vendorId]);
  }
  
  addVendor(){
    this.router.navigate(['addVendors']);
  }

  Logout() {  
    this.logout.Logout();
  }

}
