import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Vendor } from '../vendor';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AddVendorService {

  constructor(private _httpService: HttpClient) { }

  getVendor(vendorId:number): Observable<Vendor>{
    return this._httpService.get<Vendor>(environment.APIUrl +'/getVendorById/'+ vendorId);
    
  }

  addVendor(vendor:Vendor){
    let body=JSON.stringify(vendor);
    let headers=new HttpHeaders({'Content-Type':'application/json'});
    let options={headers:headers}

    if(vendor.vendorId){
      return this._httpService.put(environment.APIUrl+'/saveVendor',body,options);
    }else{
      return this._httpService.post(environment.APIUrl+'/saveVendor',body,options);
    }
  }
}
