import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Vendor } from '../vendor';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ListVendorsService {

  constructor(private _httpService: HttpClient) { }

  getAllVendors():Observable<Vendor[]>{
    return this._httpService.get<Vendor[]>(environment.APIUrl +'/listVendor');
  }

  disableVendor(vendor:Vendor){
    let body=JSON.stringify(vendor);
    let headers=new HttpHeaders({'Content-Type':'application/json'});
    let options={headers:headers}

      return this._httpService.put(environment.APIUrl+'/disableVendor',body,options);
  }

  searchVendorByString(search:string):Observable<Vendor[]>{
    return this._httpService.get<Vendor[]>(environment.APIUrl+'/vendorSearch/'+search);
  }

}
