import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AddVendorService } from './add-vendor.service';
import { Vendor } from '../vendor';
import { LogoutService } from '../logout.service';

@Component({
  selector: 'app-add-vendor',
  templateUrl: './add-vendor.component.html',
  styleUrls: ['./add-vendor.component.scss']
})
export class AddVendorComponent implements OnInit {

  vendor=new Vendor();
  constructor(private route:ActivatedRoute,private logout: LogoutService,private router:Router,private addVendorService:AddVendorService) { }

  ngOnInit() {
    this.vendor.location=undefined;
    this.route.params.subscribe(params=>this.getVendorById(params['vendorId']));
  }

  save():void{
    this.addVendorService.addVendor(this.vendor)
    .subscribe((response)=>{
     this.home();
    },(error)=>{
      console.log(error);
    });
  }
  Logout() {  
    this.logout.Logout();
  } 
  getVendorById(vendorId:number){
    this.addVendorService.getVendor(vendorId)
    .subscribe((vendorData)=>{
      this.vendor=vendorData;
    },(error)=>{
      console.log(error);
    });
  }
  

  home(){
    this.router.navigate(['listVendors']);
  }

  
}
